<template>
	<view class="warp">
		<view class="banner">
			<image src="../../static/banner.jpg" mode="widthFix"></image>
		</view>
		<view class="navbox flex-row jc">
			
			<div class="block flex-col row1 flex-1">
			    <div class="box2 flex-col">
			        <span class="info1">企服超市</span>
			        <div class="main1">
			            <span class="word">发布中</span>
			            <span class="info2">22</span>
			            <span class="word">个</span>
			        </div>
			    </div>
			</div>
			<div class="block flex-col row2 flex-1">
			    <div class="box2 flex-col">
			        <span class="info1">融资贷款</span>
			        <div class="main1">
			            <span class="word">相关产品</span>
			            <span class="info2">89</span>
			            <span class="word">个</span>
			        </div>
			    </div>
			</div>
			<div class="block flex-col row3 flex-1">
			    <div class="box2 flex-col">
			        <span class="info1">企服超市</span>
			        <div class="main1">
			            <span class="word">产业空间</span>
			            <span class="info2">89</span>
			            <span class="word">个</span>
			        </div>
			    </div>
			</div>			
		</view>
		<view class="height20"></view>
 <view class="wrap">
 	<view class="u-tabs-box bd">
 		<u-tabs-swiper activeColor="#5D7EFF" ref="tabs" :font-size="26" :list="clist" :show-bar="false" :current="current" @change="change" :is-scroll="false" swiperWidth="750"></u-tabs-swiper>
 	</view>
 	<swiper class="swiper-box" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
 		<swiper-item class="swiper-item">
 			<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
				<industry :list="list"></industry>
 			</scroll-view>
 		</swiper-item>
 		<swiper-item class="swiper-item">
 			<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
 				<industry :list="list"></industry>
 			</scroll-view>
 		</swiper-item>
 		<swiper-item class="swiper-item">
 			<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
 				<industry :list="list"></industry>
 			</scroll-view>
 		</swiper-item> 	
 	</swiper>
 </view> 
		

		
		
	</view>
</template>

<script>
	import industry from '../../components/company/industry.vue';
 
	import {
				pubPostpage,
			} from '@/api/store';
	    import {
	    		oloadingFun
	    	} from '@/utils/tools';
	    	import {
	    		loadingType
	    	} from '@/utils/type';
    var url="park/recommend/list";
	
		
	export default {
		data() {
			return {
			current: 0,
			swiperCurrent: 0,
			tabsHeight: 0,
			dx: 0,
			loadStatus: ['loadmore','loadmore','loadmore','loadmore'],
			clist:[
				{
					name:'行业资讯栏目1',
					check:true
				},
				{
					name:'行业资讯栏目2',
					check:false
				},
				{
					name:'行业资讯栏目3',
					check:false
				},
			],
			 status: loadingType.LOADING,
						total:0,
						page:1,
						list:[],
			}
		},
		components:{
			industry
		},
		onLoad() {
					this.getListFun();
				},
				onPullDownRefresh(){
					this.page++;
					this.getListFun();
				},
		methods: {
	  tabSelect(e) {
	  	this.TabCur = e.currentTarget.dataset.id;
		this.currentItem =  e.currentTarget.dataset.id;
	  	this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
	  },		// tab栏切换
		change(index) {
			this.swiperCurrent = index;
			//this.getOrderList(index);
		},
		transition({ detail: { dx } }) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({ detail: { current } }) {
			this.$refs.tabs.setFinishCurrent(current);
			this.swiperCurrent = current;
			this.current = current;
		},
			async getListFun() {
					let {
						page,
						list,
						total,
						status
					} = this;
					if (status == loadingType.FINISHED) return;
					const params = {
						 page: page,
						 searchType:"project"
					}
					var pdata={url:url,params:params};
					const data = await oloadingFun(pubPostpage, page, list, status, pdata,total)
					if (!data) return
					this.page = data.page
					this.list = data.dataList
					this.status = data.status
					this.total=data.rtotal;
				}
		}
	}
</script>

<style>
page{height: 100%;}
.banner image{width: 100%;}
.warp{background: #ffffff;}
.navbox{width: 100%; padding: 30rpx;}
.block{text-align: center; padding: 30rpx 0rpx; font-size: 28rpx; color: #333333;}
.info2{font-size: 32rpx; font-weight: bold;}
.row1{ background-color: rgba(241, 248, 244, 1);}
.row2{ background-color: rgba(254, 249, 247, 1); margin: 0rpx 30rpx;}
.row3{ background-color: rgba(242, 246, 247, 1);}
.row1 .main1{color: #178B50;}
.row2 .main1{color: #E57631;}
.row3 .main1{color: #4E5A65;}
.wrap {
	display: flex;
	flex-direction: column;
	height: calc(100vh - var(--window-top));
	width: 100%;
}
.swiper-box {
	flex: 1;
}
.swiper-item {
	height: 100%;
}
.u-scroll-bar{color: #007AFF !important;}
</style>
